.header {
  padding: 40px 0 0;
  margin: 0 0 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #3a4c5b;
}

.header__title {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 49px;
}
